<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>大棚信息管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:greenHouse:add')">新增大棚</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:greenHouse:modify')">修改大棚信息</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:greenHouse:delete')">删除大棚</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:greenHouse:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索大棚信息">
                        </div>
                            <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                        </div>
                    </div>
                    <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
                    <div v-show="inforadmin">
                        <form class="form form-horizontal col-sm-8" ref="refForm">
                            <div class="form-group">
                                <label class="control-label col-sm-2">大棚名称</label>
                                <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.greenhouse_name!='','has-error':errors.has('name')}">
                                    <input type="text" class="form-control" name="name" :placeholder="'请输入大棚名称'" v-model="addData.greenhouse_name" :data-vv-as="'大棚名称'" v-validate="$api.opreate.validateAdd('name',50,stateid,opreate,'Sensor')">
                                </div>
                                <div class="text-danger">
                                    <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-2">大棚地址</label>
                                <div class="col-sm-6" :class="{'has-success':!errors.has('address')&&addData.greenhouse_address!='','has-error':errors.has('address')}">
                                    <input type="text" class="form-control" name="address" :placeholder="'请输入大棚地址'" v-model="addData.greenhouse_address" :data-vv-as="'大棚地址'" v-validate="'required'">
                                </div>
                                <div class="text-danger">
                                    <span v-show="errors.has('address')">{{errors.first("address")}}</span>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-2">大棚用途</label>
                                <div class="col-sm-6" :class="{'has-success':!errors.has('useage')&&addData.greenhouse_useage!='','has-error':errors.has('useage')}">
                                    <textarea rows="6" class="form-control" name="useage" :placeholder="'请输入大棚用途'" v-model="addData.greenhouse_useage" :data-vv-as="'用途说明'" v-validate="'required'"></textarea>
                                </div>
                                <div class="text-danger">
                                    <span v-show="errors.has('useage')">{{errors.first("useage")}}</span>
                                </div>
                            </div>
                             <div class="form-group">
                                <label class="control-label col-sm-2">大棚简述</label>
                                <div class="col-sm-6" ><!--:class="{'has-success':!errors.has('remark')&&addData.remark!='','has-error':errors.has('remark')}"-->
                                    <textarea rows="6" class="form-control" name="remark" :placeholder="'请输入大棚简述'" v-model="addData.remark"></textarea>
                                </div>
                                <!-- <div class="text-danger">
                                    <span v-show="errors.has('useage')">{{errors.first("useage")}}</span>
                                </div> -->
                            </div>
                            <div class="form-group col-sm-12">
                                <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                                <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "greenHouseInfo/selectGreenHouseInfo.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: []
            },
            addData: {
                greenhouse_id:"",
                greenhouse_name: "", //名称
                greenhouse_useage: "", //用途
                greenhouse_address: "", //地址
                remark:""
            }
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    methods: {
        //添加按钮按下
        inforAddClick(bool) {
            this.inforadmin = bool;
            this.opreate = "add";
            for (var key in this.addData) {
                this.addData[key] = "";
            }
            this.$validator.errors.clear();
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                this.inforadmin = true;
                this.opreate = "update";
                this.addData = this.checkData[0];
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
            var bool;
            if (vm.opreate == "update") {
                bool = vm.$api.http.opreateHttp(vm, "greenHouseInfo/updateGreenHouseInfo.action", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                bool = vm.$api.http.opreateHttp(vm, "greenHouseInfo/addGreenHouseInfo.action", datas, "添加", "");
            }
            setTimeout(function () {
                vm.$validator.errors.clear();
            }, 300)
            bool.then(function (success) {
                if (success) {
                    setTimeout(function () {
                        vm.inforAddClick(false);
                    }, 800);
                }
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].greenhouse_id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "greenHouseInfo/deleteGreenHouseInfo.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入大棚名称或代号后搜索", {
                    icon: 0
                });
            }
        }
    }
};
</script>

<style>

</style>



